import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles, MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import purple from '@material-ui/core/colors/purple';
import green from '@material-ui/core/colors/green';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button'
import MenuItem from '@material-ui/core/MenuItem'
import { Redirect,Link,Route } from 'react-router-dom'
import Pricing from './Pricing'
const styles = theme => ({
  container: {
    backgroundColor:'#f3f3f4',
    paddingTop:'2%',
    
    color:'rgb(102,102,102)',
    marginTop:'6%',
    marginLeft:'23%',
    width:'750px',
    height:'525px',
    boxShadow:'rgba(0, 0, 0, 0.3) 0px 0px 10px',
    borderRadius:'5px',
    flexDirection:'column',
    justifyContent:'center'
  },
  bottomText:{
    marginTop:'2%',
    textAlign:'center',
    color:'#ffffff',
    lineHeight:'23px'
  },
  Text:{
    textAlign:'center',
    lineHeight:'23px',
    fontSize:'15px'
  },
  topText:{
    textAlign:'center',
    lineHeight:'23px',
    color:'#ffffff',
    fontWeight:'normal'
  },
   TextCenter:{
    textAlign:'center',
    fontWeight:'normal'
   }, 
  span:{
    color:'#2676a5'
  },
  Color:{
    paddingTop:'3%',
    backgroundColor:'#363B4D'
  },
  
  margin: {
    margin: 2*theme.spacing.unit,
    
  },
  button: {
    
    backgroundColor:'rgb(255, 102, 51)',
    marginLeft:'58%',
    width:'15%',
    height:'8%'
},
loginbutton:{
  marginTop:'1%',
  justify:'center',
  color:'#ffffff',
  marginLeft:'50%',
  lineHeight:'23px',
  backgroundColor:'rgba(0,0,0,.25)'
},
  title:{
    display:'flex',
    margin:'0 auto',
    color:'#ffffff'
  },
  cssLabel: {
    '&$cssFocused': {
      color: purple[500],
    },
  },
  cssFocused: {},
  cssUnderline: {
    '&:after': {
      borderBottomColor: purple[500],
    },
  },
  pricingid:{
    maxWidth:'750px',
    margin:'0 auto',
    display:'flex',
    alignItems:'center',  
    justifyContent:'space around',
    
  },
  plan:{
    flex:1,
    border:'2px ',
    backgroundColor:'rgba(0,0,0,.05)',
    borderRadius:'4px',
    margin:'10px',
    padding:'10px',
    display:'flex',
    flexWrap:'wrap',
    height:'400px',
  },
  heading:{
    margin:'0 auto',
    fontWeight:'normal',
    fontSize:'2.8em'
  },
  Top:{
    display:'flex'
  },
  signup:{  
    textAlign:'center',
    padding:'2%',
    fontWeight:'normal'
  },
 button1:{
    width:'15%',
    height:'8%',
    margin:'0 auto',
    backgroundColor:'rgba(0,0,0,.25)',
    marginLeft:'7% '
 },

  bootstrapInput: {
    borderRadius: 4,
    backgroundColor: theme.palette.common.white,
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '10px 12px',
    width: 'calc(100% - 24px)',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
    '&:focus': {
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
    },
  },
  bootstrapFormLabel: {
    fontSize: 18,
  },
  margin1:{
    margin: 2*theme.spacing.unit,
    width:'28%'
  },
  disp:{
    margin: 2*theme.spacing.unit,
    width:'60%' 
}
});
const cities=["India","Germany","France","USA"]
const theme = createMuiTheme({
  palette: {
    primary: green,
  },

});
function redirect(){
  
    <Redirect to="/pricing" >
    </Redirect>
  
    }
class Details extends Component {
   render(){ 
  const { classes } = this.props;

  return (
    <div className={classes.Color} >
    <div className={classes.topText}><h1>Invoicely</h1>
    <h2>Free, Unlimited Invoicing</h2>
    <p className={classes.Text}>Thank you for choosing invoicely. You're just a few steps away<br /> from unlimited invoicing.</p>
    </div>
    <Paper className={classes.container}>
   
    <h2  className={classes.signup}>Tell us about you</h2>
     
   <ul >
   <TextField className={classes.margin}
        
        label="First Name"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required />
       <TextField className={classes.margin}
        
        label="Last Name"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required />
       <TextField className={classes.margin}
        
        label="Company Name"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required />
       <TextField className={classes.disp}
        
        label="Address"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required />
        <TextField className={classes.margin}
        
        label="Website URL"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
     />
     <TextField className={classes.margin}
        
        label="Postal Code"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required />
       <TextField className={classes.margin}
        
        label="State"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required />
       <TextField className={classes.margin}
        
        label="Tax ID or VAT Number"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
       />
       <TextField className={classes.margin}
        
        label="City"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required />
       <TextField className={classes.margin1}
        select
        label="Country"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required>
      {cities.map(option => (
            <MenuItem key={option} value={option}>
              {option}
            </MenuItem>
          ))}
      </TextField> 
     </ul>
     <Button size="small" variant="contained" color="primary" className={classes.button1}>
        Back
      </Button>
      
      <Button size="small" variant="contained" color="primary" className={classes.button} onClick={redirect}>
        Proceed
      </Button>
      
    </Paper>
    </div>
    
  );
}
}
Details.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Details);



