import React,{Component} from 'react';
import PropTypes from 'prop-types';
import { withStyles, MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import purple from '@material-ui/core/colors/purple';
import green from '@material-ui/core/colors/green';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button'
import Pricing from './Pricing'
import Details from './Details'
import { Link,Route,Switch,Router,Redirect } from 'react-router-dom'
const styles = theme => ({
  container: {
    display: 'flex',
    backgroundColor:'#f3f3f4',
    color:'rgb(102,102,102)',
    marginTop:'6%',
    marginLeft:'36%',
    width:'380px',
    height:'499px',
    boxShadow:'rgba(0, 0, 0, 0.3) 0px 0px 10px',
    borderRadius:'5px',
    flexDirection:'column',
    justifyContent:'center'
  },
  bottomText:{
    marginTop:'2%',
    textAlign:'center',
    color:'#ffffff',
    lineHeight:'23px'
  },
  Text:{
    textAlign:'center',
    lineHeight:'23px',
    fontSize:'15px'
  },
  topText:{
    textAlign:'center',
    lineHeight:'23px',
    color:'#ffffff',
    fontWeight:'normal'
  },
   TextCenter:{
    textAlign:'center',
    fontWeight:'normal'
   }, 
  span:{
    color:'#2676a5'
  },
  Color:{
    paddingTop:'3%',
    backgroundColor:'#363B4D'
  },
  margin: {
    margin: 2*theme.spacing.unit,
    
  },
  button: {
    margin:'0 auto',
    backgroundColor:'rgb(255, 102, 51)'
},
loginbutton:{
  marginTop:'1%',
  justify:'center',
  color:'#ffffff',
  marginLeft:'47%',
  lineHeight:'23px',
  backgroundColor:'rgba(0,0,0,.25)'
},
  title:{
    display:'flex',
    margin:'0 auto',
    color:'#ffffff'
  },
  cssLabel: {
    '&$cssFocused': {
      color: purple[500],
    },
  },
  cssFocused: {},
  cssUnderline: {
    '&:after': {
      borderBottomColor: purple[500],
    },
  },
  
  bootstrapInput: {
    borderRadius: 4,
    backgroundColor: theme.palette.common.white,
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '10px 12px',
    width: 'calc(100% - 24px)',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
    '&:focus': {
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
    },
  },
  bootstrapFormLabel: {
    fontSize: 18,
  },
});

const theme = createMuiTheme({
  palette: {
    primary: green,
  },
});
function redirect(){
  return(
    <Redirect to ="/details" />
  )
}
class Signup extends Component {
   
  
  render(){
    <Router>
      <Route path="/details" component={Details} />
   </Router>
 
  const { classes } = this.props;
  
  return (
    
    <div className={classes.Color} >
    
    <div className={classes.topText}><h1>Invoicely</h1>
    <h2>Free, Unlimited Invoicing</h2>
    <p className={classes.Text}>Thank you for choosing invoicely. You're just a few steps away<br /> from unlimited invoicing.</p>
    </div>
    <Paper className={classes.container}>
         <h2 className={classes.TextCenter}>Sign Up</h2>
      <TextField className={classes.margin}
        
        label="Email address"
        id="bootstrap-input"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required />
      <TextField className={classes.margin}
        defaultValue=""
        label="Password"
        id="password-input"
        type="password"
        placeholder="Minimum 6 characters"
        InputProps={{
          disableUnderline: true,
          classes: {
            root: classes.bootstrapRoot,
            input: classes.bootstrapInput,
          },
        }}
        InputLabelProps={{
          shrink: true,
          className: classes.bootstrapFormLabel,
        }}
      required />
      <TextField className={classes.margin}
      defaultValue=""
      label="Password(confirm)"
      id="password-input"
      type="password"
      placeholder="Password must match"
      InputProps={{
        disableUnderline: true,
        classes: {
          root: classes.bootstrapRoot,
          input: classes.bootstrapInput,
        },
      }}
      InputLabelProps={{
        shrink: true,
        className: classes.bootstrapFormLabel,
      }}
    required />
    <p className={classes.Text}>By proceeding you agree <br />
to the <span className={classes.span}>Terms of Use</span> and<span className={classes.span}> Privacy Policy</span></p>

<Button variant="contained" color="primary" className={classes.button}>
        Proceed
      </Button> 
  
    </Paper>
      
     <div className={classes.bottomText}>Already a member?</div>
     <Button size="small" className={classes.loginbutton} >
          Login
        </Button>
   

    </div>
    
  );
}
}

Signup.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Signup);



